export default {
  auth: false,
  layout: 'landing',
  head: {
    title: 'About Us'
  },

  data () {
      return {
          subject: null,
          email: null,
          message:null
      }
  },
  computed: {
      responsiveBorderRadius () {
      let data = this.$vuetify.breakpoint.sm || this.$vuetify.breakpoint.xs ? 'bottom-rounded-sm' : 'bottom-rounded-lg'
      return data
      },

      responsiveCorners () {
      let data = !this.$vuetify.breakpoint.xs && !this.$vuetify.breakpoint.sm && 'rounded-xl'
      return data
      }
  },
}
